.header-container {
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  min-height: 30px; }

.header {
  flex: 1; }

.breadcrumbs-container {
  margin-left: -10px; }
  :global(.rtl) .breadcrumbs-container {
    margin-left: 0;
    margin-right: -10px; }
  .breadcrumbs-container.absolute {
    position: absolute; }

.back-button {
  position: relative;
  top: 6px;
  left: -12px;
  z-index: 1;
  width: 36px;
  height: 36px; }
  :global(.rtl) .back-button {
    left: 12px; }
  .back-button button {
    border: none;
    background-color: transparent; }
    .back-button button:hover {
      background-color: white; }
    .back-button button:active {
      margin-left: -3px;
      margin-top: -3px;
      width: 42px;
      height: 42px; }
  .back-button.dark-theme button {
    color: #FFFFFF; }
    .back-button.dark-theme button:hover {
      background-color: rgba(255, 255, 255, 0.3); }

.back-button-icon {
  display: block;
  width: 36px;
  height: 36px; }
  :global(.rtl) .back-button-icon {
    transform: scaleX(-1); }

.title-container {
  display: flex; }
  .title-container.minimized {
    position: absolute; }

.title h2 {
  margin: 0;
  height: 48px; }

.actions-bar {
  height: 30px;
  margin-top: 6px;
  margin-left: auto;
  transition: margin 0.2s cubic-bezier(0.4, 0, 0.2, 1); }
  .actions-bar.with-breadcrumbs {
    margin-top: 36px; }
  .actions-bar.minimized {
    margin-top: -3px; }
